import React, { useCallback, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './css/index.css';
import MouseOverPopover from './components/MouseOverPopover';
import OX from './components/OX';
import ShowScore from './containers/ShowScore';
import { useDispatch } from 'react-redux';

function Game() {
  const navigate = useNavigate();
  const dispatch = useDispatch(); // Result   
  const [text, setText] = useState('');

  const [showResult, setShowResult] = useState(false);
  const [display, setDisplay] = useState({}); //  
  const [gus, setGus] = useState([]); //    

  const items = [
    {
      name: '',
      src: "assets/game/.jpg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpg",
      answer: ''
    },
    {
      name: '63',
      src: "assets/game/63.jpg",
      answer: ''
    },
    {
      name: 'MBC',
      src: "assets/game/MBC.jpg",
      answer: ''
    },
  ]

  /* useEffect           ,
       
  */
  useEffect(() => {
    //  
    items.sort(() => Math.random() - 0.5);
    setDisplay(items[0]);
    setGus(items.slice(1));
  }, [])

  const __goResult = useCallback(
    () => {
      navigate('/result');
    },
    [navigate],
  )

  const __doSubmit = useCallback(
    (e) => {
      e.preventDefault(); //  

      if (text === display.answer && gus.length) {
        setShowResult(true); //  
        setTimeout(() => {
          setShowResult(false);
          dispatch({
            type : 'CORRECT'
          });
          const _gus = gus.slice(1);
          setDisplay(gus[0]);
          setGus(_gus);
          console.log(`gus length: ${gus.length}`);
        }, 1000);
      } else {
        if (text === display.answer) {
          setShowResult(true);
          dispatch({
            type : 'CORRECT'
          });
        }
        setTimeout(() => {
          __goResult();
        }, 1000);
      }
      setText('');
    }, [text, display, gus, dispatch, __goResult]
  );


  return (
    <div className='game'>
      <div className='wrapper'>
        <img className='gu-img' src={display.src} alt='' />
        <div className="score-container">
          <ShowScore />
        </div>
        <span className="map-icon">
          <MouseOverPopover />
        </span>
        <div className='quiz-container'>
          <div className='gu-name'>{display.name}</div>
          <div className='hidden-hint'>  !</div>
          <div className='input-and-result'>
            {showResult ? (
              <span className='ox-icon'>
                <OX />
              </span>
            ):(
              <form className='answer-container' onSubmit={__doSubmit}>
                <input type='text' placeholder=' ' value={text}
                  onChange={(e) => setText(e.target.value)} required />
                <button className='submit-btn' type='submit'></button>
              </form>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Game;